<template>
    <div class="content-wrapper">
        <!-- Content Header (Page header) -->
        <div class="content-header">
            <div class="container-fluid">
                <div class="row mb-2">
                    <div class="col-sm-6">
                        <h1 class="m-0">Sistema de Gestion de Expedientes Legales</h1>
                    </div><!-- /.col -->
                    <div class="col-sm-6">
                        <ol class="breadcrumb float-sm-right">
                            <router-link to="/adminlte" class="breadcrumb-item">Inicio</router-link>
                            <!-- <li class="breadcrumb-item"><a href="#">Home</a></li>
                            <li class="breadcrumb-item active">Dashboard v2</li> -->
                        </ol>
                    </div><!-- /.col -->
                </div><!-- /.row -->
            </div><!-- /.container-fluid -->
        </div>
        <!-- /.content-header -->

        <!-- Main content -->
        <section class="content">
            <div class="container-fluid">
                <!-- Info boxes -->
                <div class="row">
                    <div class="col-12 col-sm-6 col-md-3">
                        <div class="info-box">
                            <span class="info-box-icon bg-info elevation-1"><i class="fa-solid fa-bell"></i></span>

                            <div class="info-box-content">
                                <span class="info-box-text">Alertas</span>
                                <span class="info-box-number">10</span>
                            </div>
                            <!-- /.info-box-content -->
                        </div>
                        <!-- /.info-box -->
                    </div>
                    <!-- /.col -->
                    <div class="col-12 col-sm-6 col-md-3">
                        <div class="info-box mb-3">
                            <span class="info-box-icon bg-danger elevation-1"><i class="fa-solid fa-gavel"></i></span>

                            <div class="info-box-content">
                                <span class="info-box-text">Audiencias</span>
                                <span class="info-box-number">5</span>
                            </div>
                            <!-- /.info-box-content -->
                        </div>
                        <!-- /.info-box -->
                    </div>
                    <!-- /.col -->

                    <!-- fix for small devices only -->
                    <div class="clearfix hidden-md-up"></div>

                    <div class="col-12 col-sm-6 col-md-3">
                        <div class="info-box mb-3">
                            <span class="info-box-icon bg-success elevation-1"><i
                                    class="fa-solid fa-calendar-days"></i></span>

                            <div class="info-box-content">
                                <span class="info-box-text">Calendario</span>
                                <span class="info-box-number">Actividades</span>
                            </div>
                            <!-- /.info-box-content -->
                        </div>
                        <!-- /.info-box -->
                    </div>
                    <!-- /.col -->
                    <div class="col-12 col-sm-6 col-md-3">
                        <div class="info-box mb-3">
                            <span class="info-box-icon bg-warning elevation-1"><i
                                    class="fa-solid fa-layer-group text-white"></i></span>

                            <div class="info-box-content">
                                <span class="info-box-text">Expedientes</span>
                                <span class="info-box-number">2,000</span>
                            </div>
                            <!-- /.info-box-content -->
                        </div>
                        <!-- /.info-box -->
                    </div>
                    <!-- /.col -->
                </div>

                <!-- Main row -->
                <div class="row">
                    <!-- Left col -->
                    <div class="col-md-12">
                        <!-- TABLE: LATEST ORDERS -->
                        <div class="card">
                            <div class="card-header border-transparent">
                                <h3 class="card-title">Ultimas acciones realizadas</h3>

                                <div class="card-tools">
                                    <button type="button" class="btn btn-tool" data-card-widget="collapse">
                                        <i class="fas fa-minus"></i>
                                    </button>

                                </div>
                            </div>
                            <!-- /.card-header -->
                            <div class="card-body">
                                <div class="table-responsive">
                                    <table class="table table-hover" id="table" ref="myDataTable" aria-describedby="tablaHomeAdmin">
                                        <thead>
                                            <tr>
                                                <th>#</th>
                                                <th>Accion realizada</th>
                                                <th>Hora y fecha</th>
                                                <th>Usuario</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="(item, index) in actions" :key="index">
                                                <td>{{ index + 1 }}</td>
                                                <td>{{ accion(item) }}</td>
                                                <td>{{ formatFecha(item.created_at) }}</td>
                                                <td>{{ item.user.name }}</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                                <!-- /.table-responsive -->
                            </div>
                            <!-- /.card-body -->
                            <div class="card-footer clearfix">
                                <!-- <a href="javascript:void(0)" class="btn btn-sm btn-info float-left">Place New
                                    Order</a> -->
                                <a href="javascript:void(0)" class="btn btn-sm btn-secondary float-right">Ver todas las acciones</a>
                            </div>
                            <!-- /.card-footer -->
                        </div>
                        <!-- /.card -->
                    </div>
                </div>
                <!-- /.row -->
            </div><!--/. container-fluid -->
        </section>
        <!-- /.content -->
    </div>
</template>
<script>
import DataTable from '@/utils/dataTable';

export default {
    name: 'admin',
    mixins: [DataTable],
    data() {
        return {
            actions: '',
            counts: []
        };
    },
    mounted() {
        this.fecthActions();
       
    },
    methods: {
        async fecthActions() {
            try {
                this.$showLoadingAlert();
                this.actions = (await this.$postData('reportes/inicioadmin', this)).data;
                this.counts = (await this.$postData('reportes/inicioadmin', this)).count;
                await this.$waitForDataTableDraw(this, 'myDataTable');
            } catch (error) {
                this.$handleApiError(error, 'leer');
            } finally {
                this.$closeLoadingAlert();
            }
        },
        accion(item) {
            return item.accion == 'Revis√≥ el Expediente' ? `${this.formatAccion(item.accion)} : ${item.exp.exp_numero}` : this.formatAccion(item.accion);
        },
        formatAccion(accion) {
            if (accion && typeof accion === 'string') {
                return accion.charAt(0).toUpperCase() + accion.slice(1).toLowerCase();
            } else {
                return accion;
            }
        },
        formatFecha(fechaISO) {
            const fecha = new Date(fechaISO);
            const opcionesFormato = {
                year: 'numeric',
                month: 'short',
                day: 'numeric',
                hour: 'numeric',
                minute: 'numeric',
                second: 'numeric'
            };
            return fecha.toLocaleDateString('es-ES', opcionesFormato);
        }
    },
};
</script>